.wrapper_imt {
    width: 100%;
    min-height: 100vh;
    background-color: #f0f0e1;
    display: flex;
    flex-direction: column;
    padding-bottom: 25px;
    position: relative;
  }
  
  .imt_wrapper {
    padding-top: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
  
  .imt_block {
    padding-top: 50px;
  }
  
  .pictures_block {
    background-color: #ffffff;
    padding: 40px;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    width: 90%;
    max-width: 1500px;
    text-align: left;
    font-family: Circe-Regular, Arial, Helvetica, sans-serif;
  }
  
  .pictures_block_imt{
    display: flex;
    justify-content: space-around; /* Выравнивание по горизонтали */
    width: 100%;
  }
  
  .imt_column {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px; /* Расстояние между изображениями и текстом */
  }
  
  .top_picture_imt, .bottom_picture_imt {
    margin-bottom: 10px;
  }
  
  span {
    font-size: 16px;
    color: rgb(73, 116, 33);
    text-align: center;
  }
  
  .imt_tanner {
    font-family: Circe-Regular, Arial, Helvetica, sans-serif;
    font-size: 28px;
    line-height: 60px;
    margin-top: 0;
  }
  
  .imt_img{
    display: flex;
    width: 350px;
    height: 250px;
    justify-content: center;
    align-items: center;
  }
  
  .form_wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
    gap: 20px;
  }
  
  .form_group {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    gap: 5px;
  }
  
  .form_input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  .button_calculate {
    background-color: #28a745; /* Green background for the button */
    color: white; /* White text */
    border: none; /* No border */
    border-radius: 8px; /* Rounded corners */
    padding: 11px 19px; /* Padding */
    font-size: 18px;
    cursor: pointer;
    transition: background-color 0.3s ease; /* Smooth hover effect */
    height: 100%; /* Make the button the same height as input fields */
    margin-top: 28px;
  }
  
  .button_calculate:hover {
    background-color: #218838; /* Darker green on hover */
  }
  
  .imt_lead{
    margin-bottom: 0px;
  }
  
  .imt_content_item{
    padding-top: 5px;
  }
  
  
  
  .row-item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    box-sizing: border-box;
  }
  
  .column-item-header {
    box-sizing: border-box;
    width: 25%;
    padding: 19px 0;
    border-bottom: 1px solid;
    font-size: 18px;
    line-height: 23px;
  }
  
  .row-item:last-child .column-item{
    border-bottom: None;
  }
  
  .row-item:first-child .column-item{
    font-weight: bolder;
  }
  
  .column-item {
    box-sizing: border-box;
    width: 25%;
    padding: 19px 10px;
    font-size: 16px;
    line-height: 22px;
  }
  
  .container {
    box-sizing: border-box;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    margin-bottom: 60px;
    width: auto;
  }
  